<template>
  <v-container>
    <v-textarea v-model="input" label="Nhp cc trng" rows="5"></v-textarea>

    <!-- Nt th 1: To @JsonProperty -->
    <v-btn
      @click="generateJsonProperty"
      style="
        color: white;
        font-weight: bold;
        width: 200px;
        padding: 10px;
        margin: 10px;
        border: 1px solid #bbbbbb !important;
        border-radius: 4px;
        box-sizing: border-box;
        font-size: 13px;
        background-color: rgb(38, 113, 184) !important;
      "
      >To @JsonProperty</v-btn
    >

    <!-- Nt th 2: To JSON object -->
    <v-btn
      @click="generateJsonObject"
      style="
        color: white;
        font-weight: bold;
        width: 200px;
        padding: 10px;
        margin: 10px;
        border: 1px solid #bbbbbb !important;
        border-radius: 4px;
        box-sizing: border-box;
        font-size: 13px;
        background-color: rgb(38, 113, 184) !important;
      "
      >To JSON Object</v-btn
    >

    <!-- Nt th 3: To body. format -->
    <v-btn
      @click="generateBodyFormat"
      style="
        color: white;
        font-weight: bold;
        width: 200px;
        padding: 10px;
        margin: 10px;
        border: 1px solid #bbbbbb !important;
        border-radius: 4px;
        box-sizing: border-box;
        font-size: 13px;
        background-color: rgb(38, 113, 184) !important;
      "
      >To body. format</v-btn
    >

    <!-- Kt qu xut ra -->
    <v-textarea
      v-model="output"
      label="Kt qu"
      readonly
      rows="10"
    ></v-textarea>
  </v-container>
</template>

<script>
export default {
  data() {
    return {
      input: "", // Chui u vo t textarea
      output: "", // Kt qu sinh ra
    };
  },
  methods: {
    // Hm loi b "ps", "PS", "p0", "P0"
    cleanField(field) {
      return field.replace(/^(ps|PS|p0|P0)/, ""); // Loi b tin t "ps", "PS", "p0", "P0"
    },

    // Hm sinh ra @JsonProperty
    generateJsonProperty() {
      const fields = this.input
        .split("\n")
        .filter((field) => field.trim() !== "");
      const cleanedFields = fields.map(this.cleanField); // Loi b tin t trc khi x l
      const result = cleanedFields
        .map(
          (field) =>
            `@JsonProperty("${field.trim()}")\nprivate String ${field.trim()};`
        )
        .join("\n\n");
      this.output = result;
    },

    // Hm sinh ra JSON object
    generateJsonObject() {
      const fields = this.input
        .split("\n")
        .filter((field) => field.trim() !== "");
      const cleanedFields = fields.map(this.cleanField); // Loi b tin t trc khi x l
      const result = cleanedFields
        .map((field) => `"${field.trim()}": ""`)
        .join(",\n");
      this.output = `{\n${result}\n}`;
    },

    // Hm sinh ra body.MA_HD, body.MA_KH, body.MA_CQ v dng cha du '?'
    generateBodyFormat() {
      const fields = this.input
        .split("\n")
        .filter((field) => field.trim() !== "");
      const cleanedFields = fields.map(this.cleanField); // Loi b tin t trc khi x l
      const resultBody = cleanedFields
        .map((field) => `body.${field.trim()}`)
        .join(",\n");
      const resultQuestionMarks = cleanedFields.map(() => "?").join(", ");
      this.output = `${resultBody},\n${resultQuestionMarks}`;
    },
  },
};
</script>
