                        >
                          mdi-close
                        </v-icon></v-card-title
                      >
                      <v-card-text>
                        <v-img :src="currentImage" alt="Description" />
                      </v-card-text>
                      <v-card-actions>
                        <v-spacer></v-spacer>
                        <v-btn text @click="dialog = false">ng</v-btn>
                      </v-card-actions>
                    </v-card>
                  </v-dialog>

                  <v-dialog
                    v-model="notificationDialog"
