                          "
                          text
                          @click="notificationDialog = false"
                          >Close</v-btn
                        >
                      </v-card-actions>
                    </v-card>
                  </v-dialog>

                  <v-dialog
                    v-model="dialogDV"
                    class="custom-dialog"
                    persistent
                    max-width="90%"
                    :style="imageStyle"
                  >
                    <v-card>
                      <v-card-title class="headline" style="margin-bottom: 20px"
                        >Dialog n v<v-icon
                          :style="closeIconStyle"
                          @click="dialogDV = false"
                        >
                          mdi-close
                        </v-icon></v-card-title
                      >
                      <v-card-text>
                        <v-text-field
                          v-model="searchDV"
                          label="Tm kim..."
