                          outlined
                          dense
                        ></v-text-field>

                        <v-data-table
                          :headers="headersDV"
                          :items="itemsdDV"
                          :search="searchDV"
                          class="elevation-1"
                        ></v-data-table>
                      </v-card-text>
                      <v-card-actions>
                        <v-spacer></v-spacer>
                        <v-btn text @click="dialog = false">ng</v-btn>
                      </v-card-actions>
                    </v-card>
                  </v-dialog>

                  <textarea
                    class="textarea"
                    type="text"
                    id="igateToken"
                    v-model="igateToken"
                    required
                    style="
                      width: 100%;
                      height: 100px;
                      box-sizing: border-box;
                      border: 1px solid #bbbbbb !important;
                    "
                  ></textarea>
                </div>
              </v-col>
              <v-col
                cols="12"
                md="2"
                xs="12"
                class="d-flex flex-column justify-center"
              >
                <v-btn
                  @click="getToken"
                  :disabled="loading"
                  variant="outlined"
                  style="
                    color: white;
                    font-weight: bold;
                    width: auto;
                    padding: 20px;
                    margin: 10px 0;
                    border: 1px solid #bbbbbb !important;
                    border-radius: 4px;
                    box-sizing: border-box;
                    font-size: 16px;
                    background-color: #006400 !important;
                  "
                  >Ly token</v-btn
                >
              </v-col>
            </v-row>

            <v-row>
              <!-- <label for="maHso" style="font-size: large; font-weight: bold"
                >M h s:</label
              > -->

              <!-- <input
                type="text"
                id="maHso"
                v-model="maHso"
                required
                style="
                  width: auto;
                  padding: 10px;
                  margin: 10px;
                  border: 1px solid #bbbbbb !important;
                  border-radius: 4px;
                  box-sizing: border-box;
                  font-size: 16px;
                "
              />
              <span v-if="maHso" @click="clearText" class="clear-icon"></span> -->
              <v-col cols="12" md="6" sm="8">
                <v-text-field
                  label="M H s"
                  v-model="maHso"
                  outlined
                  append-icon="mdi-close-circle"
                  @click:append="clearText"
                ></v-text-field>
              </v-col>
              <v-col cols="12" md="6" sm="2">
                <v-icon
                  class="ml-2 cursor-pointer round-icon"
                  @click="extractMaHo"
                  :style="iconStyle"
                >
                  mdi mdi-atom
                </v-icon>
              </v-col>
            </v-row>
            <p v-if="eformData" style="color: red; display: none">
              eformData: {{ eformData }}
            </p>
            <v-btn
              @click="fetchData"
              :disabled="loading"
              variant="outlined"
              style="
                color: white;
                font-weight: bold;
                width: auto;
                padding: 20px;
                margin: 10px 0;
                border: 1px solid #bbbbbb !important;
                border-radius: 4px;
                box-sizing: border-box;
                font-size: 16px;
                background-color: rgb(38, 113, 184) !important;
              "
            >
              <v-progress-circular
                v-if="loading"
                indeterminate
                color="white"
                size="20"
                class="ml-2"
              ></v-progress-circular>
              Ly d liu h s
            </v-btn>
            <v-btn
              @click="endHoSo"
              :disabled="loading"
              variant="outlined"
              style="
                color: white;
                font-weight: bold;
                width: auto;
                padding: 20px;
                margin: 10px 0;
                border: 1px solid #bbbbbb !important;
                border-radius: 4px;
                box-sizing: border-box;
                font-size: 16px;
                background-color: #ce7a58 !important;
              "
              >Kt thc h s</v-btn
            >
            <p v-if="donViNop" style="color: red">
              <b>H s ca n v: {{ donViNop }}</b>
            </p>
            <p v-if="noiDangKy" style="color: red">
              <b>Ni ng k trong eform: {{ noiDangKy }}</b>
            </p>
          </section>
          <section class="mt-5">
            <h2 class="mb-5">y qua h tch</h2>
            <v-btn
              @click="pushHoTich"
              :disabled="loading"
              variant="outlined"
              style="
                color: white;
                width: auto;
                font-weight: bold;
                padding: 20px;
                margin: 10px 0;
                border: 1px solid #bbbbbb !important;
                border-radius: 4px;
                box-sizing: border-box;
                font-size: 16px;
                background-color: rgb(38, 113, 184) !important;
              "
            >
              y qua h tch
            </v-btn>
            <v-btn
              @click="showDialogDV"
              variant="outlined"
              style="
                color: white;
                width: auto;
                font-weight: bold;
                padding: 20px;
                margin: 10px 0;
                border: 1px solid #bbbbbb !important;
                border-radius: 4px;
                box-sizing: border-box;
                font-size: 16px;
                background-color: rgb(38, 113, 184) !important;
              "
            >
              Tra Cu M n v
            </v-btn>
            <p v-if="requestBody" style="color: red; display: none">
              Body gi i: {{ requestBody }}
            </p>
            <textarea
              v-model="requestBodyString"
              style="
                width: 100%;
                height: 300px;
                box-sizing: border-box;
                border: 1px solid #bbbbbb !important;
              "
            ></textarea>
            <p v-if="responseHT" style="color: purple">
              Kt qu tr v t h tch: {{ responseHT }}
            </p>
          </section>
        </section>
      </v-container>
    </v-app>
  </div>
