<script>
import axios from "axios";
import { jsonToXml } from "./jsonToXml.js";
import dviJson from "./dviJson.json";
import noiDangKyJson from "./noiDangKy.json";
import maDVHotTich from "./maDVHotTich.json";

export default {
  components: {},
  watch: {},
  computed: {
    iconStyle() {
      return {
        backgroundColor: "#1976d2 !important" /* Mu nn ca icon */,
        color: "white" /* Mu ca icon */,
        borderRadius: "50%" /* Bo trn  to hnh trn */,
        padding: "10px" /* Thm khng gian xung quanh icon */,
        fontSize: "25px" /* Kch thc icon */,
        marginBottom: "5px",
      };
    },
    closeIconStyle() {
      return {
        position: "absolute" /* t v tr tuyt i cho icon ng */,
        right: "16px" /* Cn chnh t bn phi */,
        top: "16px" /* Cn chnh t trn cng */,
        cursor: "pointer" /* Thay i con tr khi hover */,
        color: "white" /* Mu ca icon, c th thay i theo  mun */,
        borderRadius: "50%",
        backgroundColor: "#1976d2 !important",
        padding: "5px" /* Thm khng gian xung quanh icon */,
        fontSize: "15px" /* Kch thc icon */,
      };
    },

    imageStyle() {
      return {
        width: "100%",
        height: "100%",
        display: "flex",
        alignItems: "center",
        justifyContent: "center",
      };
    },
  },
  data() {
    return {
      username: "",
      password: "",
      token: "",
      maHso: "",
      hsoId: "",
      igateToken: "",
      eformData: "",
      requestBody: {
        maDonVi: "",
        module: "",
        maHoSo: "",
        ngayTiepNhan: "",
        data: "",
      },
      responseHT: "",
      requestBodyString: "",
      dialog: false,
      dialogDV: false,
      currentImage: "",
      notificationDialog: false,
      notificationMessage: "",
      isSuccess: false,
      loading: false,
      dViData: dviJson,
      noiDangKyJson: noiDangKyJson,
      donViNop: "",
      noiDangKy: "",
      ltRequest: {},
      isDVCLT: false,
      headersDV: [
        { text: "Tnh Thnh Ph", value: "Tnh Thnh Ph" },
        { text: "M TP", value: "M TP" },
        { text: "Qun Huyn", value: "Qun Huyn" },
        { text: "M QH", value: "M QH" },
        { text: "Phng X", value: "Phng X" },
        { text: "M PX", value: "M PX" },
        { text: "Cp", value: "Cp" },
      ],
      itemsdDV: maDVHotTich,
      searchDV: "",
    };
  },
  methods: {
    copyToClipboard(type) {
      var textarea = "";
      if (type == 1) {
        textarea = document.getElementById("gliToken");
      } else {
        textarea = document.getElementById("igateToken");
      }

      if (textarea) {
        textarea.select();
        document.execCommand("copy");
      }
    },

    extractMaHo() {
      const match = this.maHso.match(/(H21|G22)\.\d{2}\.\d{2}-\d{6}-\d{4}/);
      if (match) {
        this.maHso = match[0]; // Gn li ch phn cn ly
      } else {
        alert("Khng tm thy m h s hp l!");
      }
    },

    showImage(image) {
      this.currentImage = image; // Cp nht ng dn hnh nh
      this.dialog = true; // M dialog
    },

    showDialogDV() {
      this.dialogDV = true; // M dialog
    },

    showError() {
      this.notificationDialog = true; // M dialog
    },

    getB64Auth(user, password) {
      const source = user + ":" + password;
      const Authorization = "Basic " + Buffer.from(source).toString("base64");
      return Authorization;
    },
    clearText() {
      this.maHso = ""; // Xa ni dung input
    },

    async getToken() {
      try {
        const response = await axios.post(
          "https://wsms.gialai.vnpt.vn/wsms/api/igate/login-congdvc-gli",
          null,
          {
            headers: {},
          }
        );
        console.log("Token:", response.data);
        this.token = response.data.access_token;

        const response2 = await axios.post(
          "https://wsms.gialai.vnpt.vn/wsms/api/igate/login-igate",
          null,
          {
            headers: {},
          }
        );
        console.log("Token:", response2.data);
        this.igateToken = response2.data.access_token;
      } catch (error) {
        if (error.response) {
          console.error("D liu phn hi li:", error.response.data);
          console.error("Trng thi phn hi li:", error.response.status);
          console.error("Header phn hi li:", error.response.headers);
        } else if (error.request) {
          // Yu cu  c thc hin nhng khng nhn c phn hi
          console.error("Yu cu li:", error.request);
        } else {
          // Mt li  xy ra trong qu trnh thit lp yu cu
          console.error("Thng bo li:", error.message);
        }
        console.error("Cu hnh li:", error);
      }
    },

    async getLTRequest() {
      const maHsoTrimmed = this.maHso.trim().replace(/\s+/g, "");
      const getLTRequestURL = `https://apiigate.gialai.gov.vn/pa/judicial-civil-status/--send?code=${maHsoTrimmed}&showIntputSendEnable=true`;

      try {
        const dataLT = await axios.post(
          getLTRequestURL,
          {},
          {
            headers: {
              Authorization: `Bearer ${this.igateToken}`, // nh km token vo header
            },
          }
        );
        console.log("content LT:", dataLT);
        this.ltRequest = dataLT.data.input;
        this.requestBodyString = JSON.stringify(this.ltRequest, null, 2);
      } catch (error) {
        this.loading = false;
        this.isSuccess = false;
        this.notificationMessage = "Get thng tin h s khng thnh cng";
        this.showError();
        if (error.response) {
          console.error("D liu phn hi li:", error.response.data);
          console.error("Trng thi phn hi li:", error.response.status);
        } else if (error.request) {
          console.error("Yu cu li:", error.request);
        } else {
          console.error("Thng bo li:", error.message);
        }
      }
    },

    async getHsoId() {
      const maHsoTrimmed = this.maHso.trim().replace(/\s+/g, "");
      const getHsoIdUrl = `https://apiigate.gialai.gov.vn/pa/dossier/search?page=0&size=20&applicant-organization=&spec=slice&code=${maHsoTrimmed}`;
      this.requestBodyString = "";
      try {
        const getHsId = await axios.get(getHsoIdUrl, {
          headers: {
            Authorization: `Bearer ${this.igateToken}`,
          },
        });
        console.log("content:", getHsId);
        if (getHsId.data.content[0].nationCode) {
          this.maHso = getHsId.data.content[0].code;
        }
        this.hsoId = getHsId.data.content[0].id;
        this.isDVCLT = getHsId.data.content[0].isDVCLT;

        console.log(
          " getHsId.data.content[0].eForm.data.loaiHTTP",
          getHsId.data.content[0].eForm.data.loaiHTTP
        );
        if (
          getHsId.data.content[0].eForm.data.loaiHTTP == "LTKS" ||
          getHsId.data.content[0].eForm.data.loaiHTTP == "LTKT" ||
          getHsId.data.content[0].eForm.data.loaiHTTP == "LTKH"
        ) {
          await this.getLTRequest();
          this.isSuccess = true;
          this.notificationMessage = "Get thng tin h s thnh cng";
        } else {
          let agency = getHsId.data.content[0].agency.code;
          if (agency) {
            let dvi = this.dViData.content.find((room) => room.code === agency);
            this.donViNop = dvi.name;

            var maNoiDangKy = getHsId.data.content[0].eForm.data.noiDangKy;
            let noiDangKyName = this.noiDangKyJson.find(
              (data) => data.maDonViHanhChinh === maNoiDangKy
            );
            this.noiDangKy = noiDangKyName.tenDonViHanhChinh;
          }
          this.isSuccess = true;
          this.notificationMessage = "Get thng tin h s thnh cng";
          this.showError();
          this.requestBody.maDonVi =
            getHsId.data.content[0].eForm.data.noiDangKy;
          const date = new Date(getHsId.data.content[0].acceptedDate);
          const year = date.getFullYear();
          const month = String(date.getMonth() + 1).padStart(2, "0"); // Thng bt u t 0, cng 1  c thng chnh xc
          const day = String(date.getDate()).padStart(2, "0");

          this.requestBody.ngayTiepNhan = `${year}-${month}-${day}`;
          this.requestBody.module = getHsId.data.content[0].eForm.data.loaiHTTP;
          this.requestBody.maHoSo = getHsId.data.content[0].code;
          this.eformData = getHsId.data.content[0].eForm.data;
          let innerXML = jsonToXml(this.eformData);
          this.requestBody.data = `<hotich><hoso>${innerXML}</hoso></hotich>`;
          this.responseHT = "";
          this.requestBodyString = JSON.stringify(this.requestBody, null, 2);
        }
      } catch (error) {
        this.loading = false;
        this.isSuccess = false;
        this.notificationMessage = "Get thng tin h s khng thnh cng";
        this.showError();
        if (error.response) {
          console.error("D liu phn hi li:", error.response.data);
          console.error("Trng thi phn hi li:", error.response.status);
        } else if (error.request) {
          console.error("Yu cu li:", error.request);
        } else {
          console.error("Thng bo li:", error.message);
        }
      } finally {
        this.loading = false; // Kt thc hin th loader
        this.showError();
      }
    },

    async endHoSo() {
      this.loading = true;
      await this.getHsoId();
      if (this.hsoId) {
        await this.endProcess();
      }
    },

    async endProcess() {
      this.loading = true;
      if (this.hsoId == "") {
        this.loading = false;
        this.notificationMessage = "Khng c thng tin";
        this.isSuccess = false;
        this.showError();
      } else {
        const url =
          "https://apiigate.gialai.gov.vn/pa/dossier/--force-end-process";
        var endRequestBody = [
          {
            id: this.hsoId,
            code: this.maHso,
          },
        ]; // Chuyn requestBodyString v dng object
        console.log(this.requestBody);
        try {
          const response = await axios.put(url, endRequestBody, {
            headers: {
              Authorization: `Bearer ${this.igateToken}`,
              "Content-Type": "application/json", // Content-Type ca body l JSON
            },
          });
          console.log("Response:", response.data);

          this.notificationMessage = "Thnh cng";
          this.isSuccess = true;
        } catch (error) {
          this.notificationMessage = error.response
            ? error.response.data
            : error.message;
          this.isSuccess = false;
          console.error(
            "Error:",
            error.response ? error.response.data : error.message
          );
        } finally {
          this.loading = false; // Kt thc hin th loader
          this.showError();
        }
      }
    },

    async getData() {
      const url = `https://apiigate.gialai.gov.vn/pa/dossier/${this.hsoId}/--online`;
      try {
        const response = await axios.get(url, {
          headers: {
            Authorization: `Bearer ${this.igateToken}`,
          },
        });
        // Lu d liu vo bin data
        console.log("Data:", response.data);

        if (
          response.data.eForm.data.loaiHTTP == "LTKS" ||
          response.data.eForm.data.loaiHTTP == "LTKT"
        ) {
          await this.getLTRequest();
          this.isSuccess = true;
          this.notificationMessage = "Get thng tin h s thnh cng";
        } else {
          this.requestBody.maDonVi = response.data.eForm.data.noiDangKy;
          const date = new Date(response.data.acceptedDate);
          const year = date.getFullYear();
          const month = String(date.getMonth() + 1).padStart(2, "0"); // Thng bt u t 0, cng 1  c thng chnh xc
          const day = String(date.getDate()).padStart(2, "0");

          this.requestBody.ngayTiepNhan = `${year}-${month}-${day}`;
          this.requestBody.module = response.data.eForm.data.loaiHTTP;
          this.requestBody.maHoSo = response.data.code;
          this.eformData = response.data.eForm.data;
          let innerXML = jsonToXml(this.eformData);
          this.requestBody.data = `<hotich><hoso>${innerXML}</hoso></hotich>`;
          this.responseHT = "";
          this.requestBodyString = JSON.stringify(this.requestBody, null, 2);
          this.isSuccess = true;
          this.notificationMessage = "Get thng tin h s thnh cng";
          this.showError();
        }
      } catch (error) {
        this.isSuccess = false;
        this.notificationMessage = "Get thng tin h s khng thnh cng";
        if (error.response) {
          console.error("D liu phn hi li:", error.response.data);
          console.error("Trng thi phn hi li:", error.response.status);
        } else if (error.request) {
          console.error("Yu cu li:", error.request);
        } else {
          console.error("Thng bo li:", error.message);
        }
      } finally {
        this.loading = false; // Kt thc hin th loader
        this.showError();
      }
    },

    async fetchData() {
      this.loading = true;
      await this.getHsoId();
      // if (this.hsoId) {
      //   await this.getData();
      // }
    },

    async pushHoTich() {
      this.loading = true;
      if (this.token == "") {
        this.loading = false;
        this.notificationMessage = "Cha c token GLI";
        this.isSuccess = false;
        this.showError();
      } else {
        var url =
          "https://congdichvu.gialai.gov.vn:443/hotich/1.0/dangKyHoTich";
        var bodySend = JSON.parse(this.requestBodyString); // Chuyn requestBodyString v dng object

        if (
          bodySend.module == "LTKS" ||
          bodySend.module == "LTKT" ||
          bodySend.module == "LTKH"
        ) {
          url =
            "https://congdichvu.gialai.gov.vn:443/apiLienThongKSKT/1.0/nhanHoSoDKHT";
        }
        console.log(bodySend);
        try {
          const response = await axios.post(url, bodySend, {
            headers: {
              Authorization: `Bearer ${this.token}`,
              "Content-Type": "application/json", // Content-Type ca body l JSON
            },
          });
          console.log("Response:", response.data);

          this.responseHT = response.data;
          this.notificationMessage = response.data;
          this.isSuccess = true;
        } catch (error) {
          this.notificationMessage = error.response
            ? error.response.data
            : error.message;
          this.isSuccess = false;
          console.error(
            "Error:",
            error.response ? error.response.data : error.message
          );
        } finally {
          this.loading = false; // Kt thc hin th loader
          this.showError();
        }
      }
    },
  },
};
</script>

