<template>
  <div id="attach-dialog-bc5">
    <v-app>
      <v-container>
        <v-row>
          <v-col>
            <v-textarea label="Token" v-model="token" outlined></v-textarea>
          </v-col>
          <v-col>
            <!-- Textfield nhp processId -->
            <v-text-field
              label="Quy trnh ID"
              v-model="processId"
              outlined
            ></v-text-field>
          </v-col>
          <v-col>
            <v-text-field
              label="M quy trnh"
              v-model="code"
              outlined
            ></v-text-field>
          </v-col>
          <!-- Textfield nhp name -->
          <v-col>
            <v-text-field
              label="Tn quy trnh"
              v-model="name"
              outlined
            ></v-text-field>
          </v-col>
          <v-col>
            <v-btn
              @click="getProcessInfo(processId)"
              style="
                color: white;
                font-weight: bold;
                width: auto;
                padding: 10px;
                margin: 10px 0;
                border: 1px solid #bbbbbb !important;
                border-radius: 4px;
                box-sizing: border-box;
                font-size: 16px;
                background-color: rgb(38, 113, 184) !important;
              "
              >INFO QT Clone</v-btn
            >
          </v-col>
        </v-row>
        <v-row>
          <v-combobox
            v-model="selectedBranch"
            @change="getXa"
            :items="capHuyens"
            item-value="id"
            item-text="name"
            label="Chn huyn"
            attach="#attach-dialog-bc5"
          ></v-combobox>
          <v-btn
            @click="cloneProcess2"
            :disabled="loading2"
            style="
              color: white;
              font-weight: bold;
              width: auto;
              height: 40px;
              padding: 10px;
              margin: 0px 10px;
              border: 1px solid #bbbbbb !important;
              border-radius: 4px;
              box-sizing: border-box;
              font-size: 16px;
              background-color: rgb(38, 113, 184) !important;
            "
            >Clone Quy Trnh</v-btn
          >

          <!-- <v-btn
            @click="dummyFunction"
            style="
              color: white;
              font-weight: bold;
              width: auto;
              height: 40px;
              padding: 10px;
              margin: 0px 10px;
              border: 1px solid #bbbbbb !important;
              border-radius: 4px;
              box-sizing: border-box;
              font-size: 16px;
              background-color: rgb(38, 113, 184) !important;
            "
            >Reset</v-btn
          > -->
        </v-row>

        <v-row>
          <!-- Data Table hin th x -->
          <v-data-table
            v-if="filteredCommunes.length"
            :headers="communeHeaders"
            :items="filteredCommunes"
            item-value="id"
            class="mt-5"
            :items-per-page="30"
            no-data-text="Khng c x no ph hp"
            show-select
            v-model="selectedCommunes"
          >
            <template v-slot:item.tenQT="{ item }">
              <!-- Khi  c click, chuyn thnh v-text-field  chnh sa -->
              <v-text-field
                v-if="editingItemId === item.id"
                v-model="item.tenQT"
                @blur="stopEditing(item)"
                @keyup.enter="stopEditing(item)"
                label="Edit name"
                dense
                hide-details
              />
              <span v-else @click="startEditing(item)">{{ item.tenQT }}</span>
            </template>

            <template v-slot:item.maQT="{ item }">
              <!-- Khi  c click, chuyn thnh v-text-field  chnh sa -->
              <v-text-field
                v-if="editingItemId === item.id"
                v-model="item.maQT"
                @blur="stopEditing(item)"
                @keyup.enter="stopEditing(item)"
                label="Edit m QT"
                dense
                hide-details
              />
              <span v-else @click="startEditing(item)">{{ item.maQT }}</span>
            </template>
          </v-data-table>
        </v-row>
      </v-container>
    </v-app>
