  },
  methods: {
    startEditing(item) {
      this.editingItemId = item.id; // t ID ca item ang chnh sa
    },
    // Kt thc chnh sa
    stopEditing() {
      this.editingItemId = null; // t li ID, kt thc chnh sa
    },
    dummyFunction() {
      console.log("selectedCommunes", this.selectedCommunes);
    },

    getXa() {
      if (this.name != "") {
        this.filteredCommunes.forEach((item) => {
          item.editing = false;

          let shortName = item.name.replace(
            /(UBND\s+th trn|UBND\s+x|UBND\s+phng)\s+/i,
            ""
          );
          let tenTat = shortName.toLowerCase().replace(/\s+/g, "");
          item.tenTat = tenTat;
          item.tenQT =
            this.selectedBranch.tenTat + "-" + tenTat + "-" + this.name;
          item.maQT =
            this.code + "-" + this.selectedBranch.tenTat + "-" + tenTat;
