              console.log(response.data);
              this.isSuccess2 = true;
              this.notificationMessage2 = "p dng thnh cng";
              process.logoId = "hon thnh";
            } catch (error) {
              console.error(`Error for task :`, error);
              this.isSuccess2 = false;
              this.notificationMessage2 = "p dng khng thnh cng";
            } finally {
              this.loading2 = false; // Kt thc hin th loader
              this.showError2();
            }
          }
        }
      } catch (error) {
        console.error("Error cloning process:", error);
        this.isSuccess2 = false;
        this.notificationMessage2 = "p dng khng thnh cng";
      } finally {
        this.loading2 = false; // Kt thc hin th loader
